const test = require('ava');
const request = require('supertest');
const server = require('../server');

test.beforeEach(t => {
	server().start();
});

test('A first unit test', t => {
	const res = request('http://localhost:8080')
	    .get('/hosts')
		.set('Accept', 'application/json')

	t.is(res.status, 200);
	t.is
});